import { templates } from "./templates.js";
import { renderFormFields } from "./form.js";
import { updatePreview } from "./preview.js";
import { pasteToOutlook } from "./outlook.js";

Office.onReady(() => {
  const templateSelect = document.getElementById('templateSelect');
  document.getElementById("sideload-msg").style.display = "none";

  Object.keys(templates).forEach(key => {
    const option = document.createElement('option');
    option.value = key;
    option.textContent = templates[key].name;
    templateSelect.appendChild(option);
  });

  renderFormFields(templateSelect.value);
  updatePreview();

  const imageUploadContainer = document.getElementById('imageUploadContainer');
  const imageUpload = document.getElementById('imageUpload');
  let selectedImage = null;

  const updateVisibility = () => {
    if (templateSelect.value === 'keyFob') {
      imageUploadContainer.style.display = 'block';
    } else {
      imageUploadContainer.style.display = 'none';
    }
  };

  templateSelect.addEventListener('change', () => {
    renderFormFields(templateSelect.value);
    updatePreview();
    updateVisibility();
  });

  updateVisibility();

  imageUpload.addEventListener('change', (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        selectedImage = e.target.result;
        console.log('Selected image:', selectedImage);
      };
      reader.readAsDataURL(file);
    }
  });

  document.getElementById('pasteButton').addEventListener('click', () => {
    pasteToOutlook(templateSelect.value, selectedImage);
  });
});
